














.flatshow-section {
	height: 50vh;
	overflow: hidden;
}

.spin-wrap {
    min-width: 100%;
    max-height: 100vh;
    min-height: 400px;
    overflow: hidden;
    position: relative;
}
#spin {
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    left: 50%;
    bottom: 0;
}
.detail {
    display: block;
    width: 100%;
    height: 100%;
    position: relative;
    z-index: 1;
}
.detail svg {
    position: absolute;
}
.polygon,
.floor_planning {
    opacity: 0;
}
.polygon:hover,
.floor_planning:hover {
    fill: #eb5a2d;
    opacity: 0.8;
}
.nav-fly {
    position: absolute;
    width: 100%;
    top: 50%;
    left: 0;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    padding: 0 30px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    z-index: 100;
}
.nav-fly button {
    background: rgba(255, 255, 255, 0.35);
    width: 40px;
    line-height: 40px;
    text-align: center;
    vertical-align: middle;
    border: 0;
    background-color: #3586FA;
    color: #fff;
}
.nav-fly .nav-fly__right {
    float: right;
}
.spritespin-stage {
    z-index: 1000;
}
.nav-fly button {
    border: 0;
    border-radius: 8px;
    background-color: #eb5a2d;
    color: #fff;
	-webkit-transition: all 0.2s ease;
	transition: all 0.2s ease;
}
.nav-fly__right:hover {
    background: -webkit-gradient(linear,left top, right top,from(rgba(235, 90, 45, 0)), to(#eb5a2d));
    background: linear-gradient(to right,rgba(235, 90, 45, 0), #eb5a2d);
}
.nav-fly__left:hover {
    background: -webkit-gradient(linear,right top, left top,from(rgba(235, 90, 45, 0)), to(#eb5a2d));
    background: linear-gradient(to left,rgba(235, 90, 45, 0), #eb5a2d);
}






































































































































































































































































